@import url(https://fonts.googleapis.com/css?family=Lora);
* {
  margin: 0;
  padding: 0;
  font-family: "Lora", serif;
  box-sizing: border-box;
}
html {
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  /* color: #cdcdcd; */
  height: 100%;
}
/*custom search*************/
.wrap {
  color: #323232;
}

input[type="text"] {
  height: 60px;
  font-size: 28px;
  display: inline-block;
  font-family: "Lato";
  font-weight: 100;
  border: none;
  outline: none;
  color: #323232;
  padding: 3px;
  padding-right: 60px;
  width: 0px;
  position: absolute;
  top: 0;
  right: 0;
  background: none;
  z-index: 3;
  transition: width 0.4s cubic-bezier(0, 0.4, 0, 1);
  cursor: pointer;
}

input[type="text"]:focus:hover {
  border-bottom: 1px solid #bbb;
}

input[type="text"]:focus {
  width: 400px;
  z-index: 1;
  cursor: text;
}
input[type="submit"] {
  height: 67px;
  width: 63px;
  display: inline-block;
  color: red;
  /* float: right; */
  background: url(data:image/png;base64,)
    center center no-repeat;
  text-indent: -10000px;
  border: none;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  cursor: pointer;
  opacity: 0.4;
  cursor: pointer;
  transition: opacity 0.4s ease;
}

input[type="submit"]:hover {
  opacity: 0.8;
}
/*end of custom search*/

.article {
  display: grid;
  grid-template-columns: 1fr 3fr;
  outline: 1px solid #cdcdcd;
  max-height: 150px;
}
.user {
  display: inline-block;
  margin: 5px;
  padding: 10px;
  background-color: #323232;
  color: #cdcdcd;
  box-shadow: 5px 5px 5px #32323275;
  text-align: center;
  border-right: 1px solid #cdcdcd;
}
.tweet {
  font-size: 12px;
  margin: 5px;
  background-color: #323232;
  color: #cdcdcd;
  box-shadow: 5px 5px 5px #32323275;
  padding: 10px;
  text-align: center;
}
.postTime {
  text-align: right;
}
.retweet {
  max-width: 30px;
  display: block;
  margin: 5px;
}
.avatar {
  max-width: 48px;
  max-height: 48px;
  border-radius: 50%;
}

